.modal-content {
    p,
    .title {
        font-family: 'Open Sans';
    }

    .title {
        @mixin clear;

        font-size: 24px;
        font-weight: bold;
        color: $black;
        margin-bottom: 30px;
    }

    .text {
        float: left;
        order: 1;
        margin-bottom: 15px;

        @mixin desktop {
            @mixin column 7,12;
        }

        p {
            font-size: 14px;
            line-height: normal;
        }
    }

    .image {
        order: 2;

        @mixin desktop {
            @mixin column 5,12;
        }

        float: right;
        margin-right: 0;

        img {
            width: 100%;
        }
    }
}
