.section.tabbed-product-listings {
    @mixin desktop {
        padding: 60px 0 0;
    }

    .tabs,
    .tab-bodies,
    .tab-body {
        width: 100%;
        position: relative;
        height: auto !important;
    }

    .tab-body > .title {
        position: relative;
        background: $purple;
        margin: 0;
        padding: 30px 0;

        @mixin arrow $purple, 26px;

        @mixin desktop {
            display: none;
        }

        h3 {
            color: $white;
            margin: 0;
            padding: 0;
        }
    }

    .container {
        align-items: center;
        justify-content: center;
    }

    .type {
        padding-top: 40px;

        @mixin desktop {
            padding: 60px 0 20px;
        }
    }

    .tab-links {
        border-bottom: solid 4px $purple;
        margin: 0;

        .tab {
            background: #d4d4d4;
            border: 0;
            font-size: 18px;
            font-weight: 600;
            letter-spacing: -0.4px;
            text-align: center;

            a {
                color: $black;
                text-transform: capitalize;
            }

            &:hover {
                background: $gray_dark1;

                a {
                    color: $white;
                }
            }

            &.active:hover {
                background: $purple_light;
            }

            &.active {
                background: $purple;

                a {
                    color: $white;
                }
            }
        }
    }

    h3 {
        padding: 0 30px;
        text-align: center;
        margin-bottom: 30px;
    }

    .product-listing {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        padding: 30px;

        .product-item {
            margin-bottom: 30px;

            @mixin column 1, 1;

            @mixin tablet {
                @mixin column 1, 3;
            }

            @mixin desktop {
                @mixin column 1, 4;
            }
        }
    }

    .product-item {
        font-family: 'Open Sans';

        a {
            text-decoration: none;
        }

        .image {
            width: 100%;
            position: relative;

            img {
                width: 100%;
                height: 100%;
                display: block;
                object-fit: cover;
                object-position: center center;
            }

            .overlay {
                background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 43%, rgba(0, 0, 0, 0.74) 100%);
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                width: 100%;
            }

            .title {
                position: absolute;
                bottom: 5px;
                width: 100%;
                text-align: center;
            }
        }

        .stats {
            color: $white;
            padding: 10px;

            ul {
                display: flex;
            }

            li {
                width: 33.32999%;
                text-align: center;
                font-size: 19px;
                font-weight: normal;
                line-height: 1.05;
                color: $white;
            }

            .label {
                font-size: 12px;
                font-weight: bold;
                line-height: 1.67;
                text-align: center;
                color: $white;
                text-transform: uppercase;
            }
        }
    }
}
